#details {
    position: relative;
    display: flex; flex-direction: column;
}
#details .details-header {
    width: 100%; height: 100vh;
    position: relative;
}
#details .details-header-image {
    width: 100%; height: 100vh;
}
#details .details-header-image::after {
    content: ''; 
    display: block;
    position: absolute; top: 0; left: 0; right: 0; bottom: 0;
    background: linear-gradient(180deg, transparent 0,#000);
}
#details .details-header-image img {
    width: 100%; height: 100%;
    object-fit: cover;
    opacity: .65;
}
#details .details-header-details {
    position: absolute;  bottom: 0; 
    z-index: 999;
    display: flex; flex-direction: column;
    color: #fff;
    font-size: 2rem; padding: 10rem;

}
#details .details-header-details .details-header-details-title {
    font-size: 3rem; font-weight: 500;
}
#details .details-content {
    padding: 10rem 0; color: #fff;
    display: flex; flex-direction: column;
}



/* OVERVIEW  */
#details .details-content .details-content-overview {
   padding-bottom: 5rem; margin-bottom: 5rem;
   border-bottom: 2px solid #252934;
}
#details .details-content .details-content-overview .details-content-overview-title {
    font-size: 2.5rem; letter-spacing: .25rem; font-weight: 500;
    position: relative; margin-bottom: 3rem; padding-bottom: 1rem;
}
#details .details-content .details-content-overview .details-content-overview-title::after {
    content: '';
    display: block;
    width: 16rem; height: 1px;
    background: #fff;
    position: absolute;
    bottom: 0; left: 0;
}
#details .details-content .details-content-overview .details-content-overview-content {
    font-size: 1.75rem;
}



/* CAST */
#details .details-content .details-content-cast {
    padding-bottom: 5rem; margin-bottom: 5rem;
   border-bottom: 2px solid #252934;
}
#details .details-content .details-content-cast .details-content-cast-header .details-content-cast-title {
    font-size: 2.5rem; letter-spacing: .25rem; font-weight: 500;
    position: relative; margin-bottom: 3rem; padding-bottom: 1rem;
}
#details .details-content .details-content-cast .details-content-cast-header .details-content-cast-title::after {
    content: '';
    display: block;
    width: 16rem; height: 1px;
    background: #fff;
    position: absolute;
    bottom: 0; left: 0;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item {
    border: 2px solid #252934;
    height: 24.5rem;
    width: 15rem !important;
    background-color: #252934;
    position: relative;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item a {
    width: 100%;  height: 21rem;
    position: absolute;
    top: 0;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item a img {
    width: 100%;  height: 100%;
    object-fit: cover;
    opacity: .5;
    transition: all .25s linear;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item .details-content-cast-item-footer {
    position: absolute;
    bottom: 0; width: 100%;
    font-size: 1.5rem; font-weight: 400;
    display: flex; justify-content: center; align-items: center;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item .details-content-cast-item-footer p {
    margin-bottom: 0; color: #ddd; width: 100%;
    display: flex; justify-content: center;
}
#details .details-content .details-content-cast .details-content-cast-content .details-content-cast-item a img:hover {
    opacity: 1;
}
#details .details-content .details-content-cast .details-content-cast-content .slick-slide {
    width: 200px !important;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-track {
    display: flex !important; margin: 0 !important;
    align-content: center;
    position: relative;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-prev {
    z-index: 999;
    left: 95.5% !important;
    top: -25% !important;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-next {
    z-index: 999;
    left: 97.5% !important;
    top: -25% !important;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-prev::before {
    content: '❮' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-next::before {
    content: '❯' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-arrow {
    font-size: 0 !important;
    width: 0 !important; height: 0 !important;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-arrow:hover {
    color: #ddd !important;
}
#details .details-content .details-content-cast  .details-content-cast-content .slick-prev:hover::before,
#details .details-content .details-content-cast .details-content-cast-content .slick-next:hover::before {
    cursor: pointer;
    color: var(--main-red) !important;
}



/* TRAILERS */
#details .details-content .details-content-trailers {
    padding-bottom: 5rem; margin-bottom: 5rem;
   border-bottom: 2px solid #252934;
}
#details .details-content .details-content-trailers .details-content-trailers-header .details-content-trailers-title {
    font-size: 2.5rem; letter-spacing: .25rem; font-weight: 500;
    position: relative; margin-bottom: 3rem; padding-bottom: 1rem;
}
#details .details-content .details-content-trailers .details-content-trailers-header .details-content-trailers-title::after {
    content: '';
    display: block;
    width: 16rem; height: 1px;
    background: #fff;
    position: absolute;
    bottom: 0; left: 0;
}
#details .details-content .details-content-trailers-content {
    display: flex; justify-content: space-evenly; align-items: center;
}
#details .details-content .details-content-trailers .details-content-trailers-content .details-content-trailers-item {
    height: 21rem;
    position: relative;
}

#details .details-content .details-content-trailers .details-content-trailers-content .details-content-trailers-item iframe {
    width: 100%; height: 100%;
    border: none !important;
}

#details .details-content .details-content-trailers .details-content-trailers-content .slick-slide {
    height: 100% !important;
}
#details .details-content .details-content-trailers .details-content-trailers-content .slick-slide:not(:last-child) {
    margin-right: 4.5rem;
}
#details .details-content .details-content-trailers .details-content-trailers-content .slick-slider {
    width: 100%;
}
#details .details-content .details-content-trailers  .details-content-trailers-content .slick-track {
    display: flex !important; 
    align-content: center;
    position: relative;
    margin: 0 !important;
}


/* REVIEWS */
#details .details-content .details-content-reviews {
    margin-bottom: 5rem;
}
#details .details-content .details-content-reviews .details-content-reviews-header .details-content-reviews-title {
    font-size: 2.5rem; letter-spacing: .25rem; font-weight: 500;
    position: relative; margin-bottom: 3rem; padding-bottom: 1rem;
}
#details .details-content .details-content-reviews .details-content-reviews-header .details-content-reviews-title::after {
    content: '';
    display: block;
    width: 16rem; height: 1px;
    background: #fff;
    position: absolute;
    bottom: 0; left: 0;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item:not(:last-child) {
    margin-bottom: 5rem;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item {
    background: rgba(37,41,52,.8);
    border: 2px solid #1b242f;
    padding: 5rem; word-break: break-word;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item .details-content-review-item-title {
    margin-bottom: 2rem;
    font-size: 2rem; font-weight: 500;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item .details-content-review-item-content {
    font-size: 1.75rem;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item a {
    color: var(--main-red); font-size: 1.75rem;
    transition: all .3s linear;
}
#details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item a:hover {
    color: #fff; text-decoration: none;
}

#details .details-content .details-content-trailers .slick-arrow {
    font-size: 0 !important;
    width: 0 !important; height: 0 !important;
}
#details .details-content .details-content-trailers .slick-prev::before {
    content: '❮' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#details .details-content .details-content-trailers .slick-next::before {
    content: '❯' !important;
    color: #fff !important;
    opacity: 1 !important;
    font-size: 3rem !important;
    transition: all .25s linear;
}
#details .details-content .details-content-trailers .slick-prev:hover::before,
#details .details-content .details-content-trailers .slick-next:hover::before {
    cursor: pointer;
    color: var(--main-red) !important;
}





/* MEDIA QUERIES */
/* Extra small  */
@media (max-width: 575.98px) {
    #details .details-header, #details .details-header-image {
        height: 45rem;
    }
    #details .details-header-details {
        padding: 3rem;
        font-size: 1.75rem;
    }
    #details .details-header-details .details-header-details-title {
        font-size: 2.5rem;
    }
    #details .details-content {
        padding: 5rem 3rem 0;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-slide {
        width: 156px !important;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-prev,
    #details .details-content .details-content-trailers .slick-prev {
        left: 86% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-next,
    #details .details-content .details-content-trailers .slick-next {
        left: 92% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-trailers .slick-prev::before {
        content: '❮' !important;
        color: #fff !important;
        opacity: 1 !important;
        font-size: 3rem !important;
        transition: all .25s linear;
    }
    #details .details-content .details-content-trailers .slick-next::before {
        content: '❯' !important;
        color: #fff !important;
        opacity: 1 !important;
        font-size: 3rem !important;
        transition: all .25s linear;
    }
    #details .details-content .details-content-trailers .slick-prev:hover::before,
    #details .details-content .details-content-trailers .slick-next:hover::before {
    cursor: pointer;
    color: var(--main-red) !important;
    }
    #details .details-content .details-content-trailers .details-content-trailers-content .slick-track {
        width: 936px !important;
    }
    #details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item {
        padding: 2rem;
    }
}

/* Small  */
@media (min-width: 576px) and (max-width: 767.98px) {
    #details .details-header-details {
        padding: 5rem 12.5rem;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-prev, 
    #details .details-content .details-content-trailers .slick-prev {
        left: 94.5% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-next,
    #details .details-content .details-content-trailers .slick-next {
        left: 98% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-trailers .details-content-trailers-content .slick-track {
        width: 940px !important;
    }
    #details .details-content .details-content-trailers .details-content-trailers-content .slick-slide {
        width: 31rem !important;
    }
    #details .details-content .details-content-trailers .details-content-trailers-content .slick-slide:not(:last-child) {
        margin-right: 3rem;
    }
    #details .details-content .details-content-reviews .details-content-reviews-content .details-content-review-item {
        padding: 2rem;
    }
    #person-details .person-details-content .person-details-content-cast-movies .person-details-content-cast-movies-content .person-details-content-cast-movies-cast-item .person-details-content-cast-movies-cast-item-footer p span.IoIosStar svg,
    #person-details .person-details-content .person-details-content-cast-tv .person-details-content-cast-tv-content .person-details-content-cast-tv-cast-item .person-details-content-cast-tv-cast-item-footer p span.IoIosStar svg {
        margin-top: 0;
    }

}

 /* Medium  */
@media (min-width: 768px) and (max-width: 991.98px) {
    #details .details-header, #details .details-header-image {
        height: 55rem;
    }
    #details .details-header-details {
        padding: 5rem;
    }
    #details .details-content {
        padding: 10rem 2rem;
    }
    #details .details-content .details-content-overview .details-content-overview-content {
        margin: 0;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-prev, 
    #details .details-content .details-content-trailers .slick-prev {
        left: 95.5% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-next,
    #details .details-content .details-content-trailers .slick-next {
        left: 98% !important;
        top: -30% !important;
    }
}

/* Large  */
@media (min-width: 992px) and (max-width: 1199.98px) {
    #details .details-header-details {
        padding: 7rem;
    }
    #details .details-content {
        padding: 10rem 24px;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-prev, 
    #details .details-content .details-content-trailers .slick-prev {
        left: 96% !important;
        top: -30% !important;
    }
    #details .details-content .details-content-cast .details-content-cast-content .slick-next,
    #details .details-content .details-content-trailers .slick-next {
        left: 98% !important;
        top: -30% !important;
    }
}

